.parent {
  perspective: 5000px;
  width: 1000px;
  height: 800px;
  padding: 60px;
}

li {
  color: #fff;
  margin-left: 10px;
  font-size: x-large;
}

li:hover {
  color: #172fd3;
}

.card {
  transform-style: preserve-3d;
  background-color: #f0f0f0;
  background-image: linear-gradient(135deg, #0000 18.75%, #f3f3f3 0 31.25%, #0000 0), repeating-linear-gradient(45deg, #f3f3f3 -6.25% 6.25%, #fff 0 18.75%);
  background-position: 0 0, 0 0;
  background-repeat: repeat, repeat;
  background-size: 60px 60px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  border: 3px solid #fff;
  width: 100%;
  padding-top: -100px;
  transition: all .5s ease-in-out;
  box-shadow: 0 30px 30px -10px #8e8e8e4d;
}

.card:hover {
  background-position: -100px 100px, -100px 100px;
  transform: rotate3d(.5, 1, 0, 30deg);
}

.content-box {
  transform-style: preserve-3d;
  background: #04c1fabb;
  padding: 100px 25px 25px;
  transition: all .5s ease-in-out;
}

.content-box .card-title {
  color: #fff;
  font-size: 25px;
  font-weight: 900;
  transition: all .5s ease-in-out;
  display: inline-block;
  transform: translate3d(0, 0, 50px);
}

.content-box .card-title:hover {
  transform: translate3d(0, 0, 60px);
}

.content-box .card-content {
  color: #f2f2f2;
  margin-top: 10px;
  font-size: 12px;
  font-weight: 700;
  transition: all .5s ease-in-out;
  transform: translate3d(0, 0, 30px);
}

.content-box .card-content:hover {
  transform: translate3d(0, 0, 60px);
}

.content-box .see-more {
  cursor: pointer;
  text-transform: uppercase;
  color: #07b9ff;
  background: #fff;
  margin-top: 1rem;
  padding: .5rem .7rem;
  font-size: 9px;
  font-weight: 900;
  transition: all .5s ease-in-out;
  display: inline-block;
  transform: translate3d(0, 0, 20px);
}

.content-box .see-more:hover {
  transform: translate3d(0, 0, 60px);
}

.date-box {
  background: #fff;
  border: 1px solid #07b9ff;
  width: 150px;
  height: 100px;
  padding: 10px;
  position: absolute;
  top: 30px;
  right: 30px;
  transform: translate3d(0, 0, 80px);
  box-shadow: 0 17px 10px -10px #64646f33;
}

.date-box span {
  text-align: center;
  display: block;
}

.date-box .month {
  color: #04c1fa;
  font-size: 9px;
  font-weight: 700;
}

.date-box .date {
  color: #04c1fa;
  font-size: 20px;
  font-weight: 900;
}

#overlay-text {
  text-align: center;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

p.text {
  text-transform: uppercase;
  letter-spacing: .5em;
  padding: 1. em 0em;
  border: 4px double #ffffff40;
  border-width: 4px 0;
  width: 40em;
  margin: 30px 0 0 -20em;
  display: inline-block;
  position: absolute;
  top: 20%;
  left: 50%;
}

p.text span {
  letter-spacing: 0;
  text-shadow: 0 0 80px #ffffff80;
  background: url("https://i.ibb.co/XzFbNhx/7effdbe1-95b5-48a2-a4c0-cde217d6e91e.png") repeat-y;
  -webkit-text-fill-color: transparent;
  -webkit-backface-visibility: hidden;
  -webkit-background-clip: text;
  background-clip: text;
  margin: 0 auto;
  padding: .25em 0 .325em;
  font: 700 4em / 1 Oswald, sans-serif;
  -webkit-animation: 80s linear infinite aitf;
  display: block;
  -webkit-transform: translate3d(0, 0, 0);
}

@-webkit-keyframes aitf {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 100%;
  }
}

